// Custom expandable node component
const ExpandableNode = ({ data, id }) => {
  const handleExpand = (event) => {
    event.stopPropagation();
    if (data.onExpand) {
      data.onExpand(data.shipIndex, !data.isExpanded);
    }
  };

  return (
    <div 
      className={`px-6 py-4 rounded-lg border-2 min-w-[150px] text-center cursor-pointer transition-all shadow-lg ${
        data.isShipRoot 
          ? 'bg-gradient-to-br from-blue-600 to-blue-700 text-white border-blue-800 hover:from-blue-700 hover:to-blue-800 transform hover:scale-105' 
          : data.nodeType === 'system'
          ? 'bg-gradient-to-br from-green-500 to-green-600 text-white border-green-700'
          : data.nodeType === 'equipment'
          ? 'bg-gradient-to-br from-purple-500 to-purple-600 text-white border-purple-700'
          : 'bg-gradient-to-br from-gray-100 to-gray-200 border-gray-400 hover:from-gray-200 hover:to-gray-300'
      }`}
      onClick={data.isShipRoot ? handleExpand : undefined}
    >
      <div className="font-bold text-lg">{data.label}</div>
      {data.isShipRoot && (
        <div className="text-sm mt-2 opacity-90">
          <div className="flex items-center justify-center gap-2">
            <span>{data.isExpanded ? '🔼' : '🔽'}</span>
            <span>{data.isExpanded ? 'Click to collapse' : 'Click to expand'}</span>
          </div>
          <div className="mt-1 text-xs bg-white/20 rounded px-2 py-1 inline-block">
            {data.nodeCount} components
          </div>
        </div>
      )}
      {data.description && (
        <div className="text-xs mt-2 opacity-80">
          {data.description}
        </div>
      )}
    </div>
  );
};

const nodeTypes = {
  expandable: ExpandableNode,
};

// Layout algorithm for hierarchical positioning
const getLayoutedElements = (nodes, edges, direction = 'TB') => {
  const isHorizontal = direction === 'LR';
  
  // Find root nodes (ship nodes)
  const shipNodes = nodes.filter(node => node.data?.isShipRoot);
  const detailNodes = nodes.filter(node => !node.data?.isShipRoot);
  
  // Position ship nodes in a row
  const shipSpacing = 400;
  const layoutedShipNodes = shipNodes.map((node, index) => ({
    ...node,
    position: {
      x: index * shipSpacing,
      y: 0
    },
    targetPosition: Position.Top,
    sourcePosition: Position.Bottom,
  }));
  
  // Position detail nodes below their respective ships
  const layoutedDetailNodes = detailNodes.map(node => {
    const shipIndex = node.data?.shipIndex || 0;
    const baseX = shipIndex * shipSpacing;
    
    // Get all detail nodes for this ship
    const shipDetailNodes = detailNodes.filter(n => n.data?.shipIndex === shipIndex);
    const nodeIndex = shipDetailNodes.findIndex(n => n.id === node.id);
    
    // Create a grid layout for detail nodes
    const nodesPerRow = 3;
    const nodeSpacing = 180;
    const rowSpacing = 120;
    
    const row = Math.floor(nodeIndex / nodesPerRow);
    const col = nodeIndex % nodesPerRow;
    
    // Center the nodes under the ship
    const totalWidth = Math.min(shipDetailNodes.length, nodesPerRow) * nodeSpacing;
    const startX = baseX - totalWidth / 2 + nodeSpacing / 2;
    
    return {
      ...node,
      position: {
        x: startX + col * nodeSpacing,
        y: 200 + row * rowSpacing
      },
      targetPosition: Position.Top,
      sourcePosition: Position.Bottom,
    };
  });
  
  return { 
    nodes: [...layoutedShipNodes, ...layoutedDetailNodes], 
    edges 
  };
};

// Main component
const FleetDiagramsApp = ({ data = mockData }) => {
  const [nodes, setNodes, onNodesChange] = useNodesState([]);
  const [edges, setEdges, onEdgesChange] = useEdgesState([]);
  const [expandedShips, setExpandedShips] = useState(new Set());
  const { fitView } = useReactFlow();

  const ships = data?.ships || [];

  const onConnect = useCallback(
    (params) => setEdges((eds) => addEdge(params, eds)),
    [setEdges]
  );

  // Handle ship expansion/collapse
  const handleShipExpand = useCallback((shipIndex, isExpanding) => {
    const newExpandedShips = new Set(expandedShips);
    
    if (isExpanding) {
      newExpandedShips.add(shipIndex);
    } else {
      newExpandedShips.delete(shipIndex);
    }
    
    setExpandedShips(newExpandedShips);
  }, [expandedShips]);

  // Prepare nodes and edges for expandable view
  const prepareFleetData = useCallback(() => {
    if (!ships || ships.length === 0) return { nodes: [], edges: [] };

    // Create ship root nodes
    const shipRootNodes = ships.map((ship, index) => {
      const shipData = ship?.reactflow || {};
      const shipNodes = shipData.nodes || [];
      const isExpanded = expandedShips.has(index);
      
      return {
        id: `ship-root-${index}`,
        type: 'expandable',
        position: { x: index * 400, y: 0 },
        data: {
          label: shipData.metadata?.ship_name || `Ship ${index + 1}`,
          isShipRoot: true,
          isExpanded: isExpanded,
          nodeCount: shipNodes.length,
          onExpand: handleShipExpand,
          shipIndex: index,
          description: `${shipNodes.length} components`
        }
      };
    });

    // Add expanded ship details
    let allNodes = [...shipRootNodes];
    let allEdges = [];

    expandedShips.forEach(shipIndex => {
      const ship = ships[shipIndex];
      
      if (ship?.reactflow) {
        const shipData = ship.reactflow;
        const shipNodes = shipData.nodes || [];
        const shipEdges = shipData.edges || [];

        // Add ship detail nodes with original IDs
        const expandedNodes = shipNodes.map((node) => ({
          ...node,
          id: `expanded-${shipIndex}-${node.id}`, // Keep original IDs but prefix to avoid conflicts
          type: 'expandable',
          data: {
            ...node.data,
            isShipRoot: false,
            shipIndex: shipIndex,
            nodeType: getNodeType(node.data?.label || ''),
            description: `Ship: ${shipData.metadata?.ship_name || `Ship ${shipIndex + 1}`}`
          }
        }));

        // Add ship internal edges
        const internalEdges = shipEdges.map(edge => ({
          ...edge,
          id: `expanded-${shipIndex}-${edge.id}`,
          source: `expanded-${shipIndex}-${edge.source}`,
          target: `expanded-${shipIndex}-${edge.target}`,
          style: { 
            stroke: '#64748b', 
            strokeWidth: 2,
            strokeDasharray: '5,5'
          }
        }));

        // Add connection from ship root to detail nodes
        const connectionEdges = shipNodes.map(node => ({
          id: `connection-${shipIndex}-${node.id}`,
          source: `ship-root-${shipIndex}`,
          target: `expanded-${shipIndex}-${node.id}`,
          type: 'smoothstep',
          style: { 
            stroke: '#3b82f6', 
            strokeWidth: 3,
            opacity: 0.6
          },
          animated: true
        }));

        allNodes = [...allNodes, ...expandedNodes];
        allEdges = [...allEdges, ...internalEdges, ...connectionEdges];
      }
    });

    return { nodes: allNodes, edges: allEdges };
  }, [ships, expandedShips, handleShipExpand]);

  // Determine node type for styling
  const getNodeType = (label) => {
    const lowerLabel = label.toLowerCase();
    if (lowerLabel.includes('system') || lowerLabel.includes('control')) return 'system';
    if (lowerLabel.includes('generator') || lowerLabel.includes('engine') || lowerLabel.includes('turbine')) return 'equipment';
    return 'component';
  };

  // Layout and update
  const onLayout = useCallback((direction) => {
    const fleetData = prepareFleetData();
    const { nodes: layoutedNodes, edges: layoutedEdges } = getLayoutedElements(
      fleetData.nodes,
      fleetData.edges,
      direction
    );

    setNodes([...layoutedNodes]);
    setEdges([...layoutedEdges]);

    setTimeout(() => {
      fitView({ padding: 0.2 });
    }, 100);
  }, [prepareFleetData, setNodes, setEdges, fitView]);

  // Initialize
  useEffect(() => {
    if (ships.length > 0) {
      const fleetData = prepareFleetData();
      const { nodes: layoutedNodes, edges: layoutedEdges } = getLayoutedElements(
        fleetData.nodes,
        fleetData.edges,
        'TB'
      );
      
      setNodes(layoutedNodes);
      setEdges(layoutedEdges);
      
      setTimeout(() => {
        fitView({ padding: 0.2 });
      }, 100);
    }
  }, [ships, expandedShips, prepareFleetData, setNodes, setEdges, fitView]);

  if (!ships || ships.length === 0) {
    return (
      <div className="flex items-center justify-center h-screen bg-gray-100">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-gray-700 mb-2">No Fleet Data Available</h2>
          <p className="text-gray-500">Please provide ship data to visualize the fleet.</p>
        </div>
      </div>
    );
  }

  return (
    <div className="h-screen w-full bg-gradient-to-br from-gray-50 to-gray-100">
      {/* Header */}
      <div className="p-4 bg-gradient-to-r from-gray-900 to-gray-800 text-white border-b shadow-lg">
        <div className="flex justify-between items-center">
          <div>
            <h1 className="text-3xl font-bold">🚢 Fleet Command Center</h1>
            <p className="text-sm text-gray-300 mt-1">
              Ships: {ships.length} | Active Nodes: {nodes.length} | Connections: {edges.length}
            </p>
            <div className="flex gap-3 mt-2">
              {ships.map((ship, index) => (
                <span 
                  key={index} 
                  className={`px-3 py-1 rounded-full text-xs font-medium transition-colors ${
                    expandedShips.has(index) 
                      ? 'bg-green-500 text-white' 
                      : 'bg-gray-600 text-gray-300'
                  }`}
                >
                  {ship?.reactflow?.metadata?.ship_name || `Ship ${index + 1}`}
                  {expandedShips.has(index) && ' ✓'}
                </span>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* ReactFlow Canvas */}
      <div className="h-full">
        <ReactFlow
          nodes={nodes}
          edges={edges}
          onNodesChange={onNodesChange}
          onEdgesChange={onEdgesChange}
          onConnect={onConnect}
          nodeTypes={nodeTypes}
          fitView
          attributionPosition="bottom-left"
          connectionMode={ConnectionMode.Loose}
          minZoom={0.1}
          maxZoom={1.5}
        >
          <Controls position="top-left" />
          <Background variant="dots" gap={20} size={1} />
          <MiniMap 
            position="bottom-right"
            nodeColor={(node) => {
              if (node.data?.isShipRoot) return '#2563eb';
              if (node.data?.nodeType === 'system') return '#059669';
              if (node.data?.nodeType === 'equipment') return '#7c3aed';
              return '#6b7280';
            }}
            nodeStrokeWidth={2}
            zoomable
            pannable
          />
          <Panel position="top-right">
            <div className="bg-white p-4 rounded-lg shadow-xl border border-gray-200">
              <h3 className="font-bold mb-3 text-gray-800">🎛️ Controls</h3>
              <div className="flex flex-col gap-2 mb-4">
                <button
                  onClick={() => onLayout('TB')}
                  className="px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg text-sm transition-colors font-medium"
                >
                  📋 Top-Bottom Layout
                </button>
                <button
                  onClick={() => onLayout('LR')}
                  className="px-4 py-2 bg-green-600 hover:bg-green-700 text-white rounded-lg text-sm transition-colors font-medium"
                >
                  ↔️ Left-Right Layout
                </button>
              </div>
              <div className="pt-3 border-t border-gray-200">
                <p className="text-xs text-gray-600 mb-2">
                  <strong>💡 How to use:</strong>
                </p>
                <ul className="text-xs text-gray-600 space-y-1">
                  <li>🖱️ Click ship nodes to expand</li>
                  <li>🔍 Use minimap to navigate</li>
                  <li>⚙️ Drag nodes to reposition</li>
                </ul>
              </div>
              <div className="pt-3 border-t border-gray-200 mt-3">
                <p className="text-xs text-gray-600 mb-1">
                  <strong>Legend:</strong>
                </p>
                <div className="space-y-1 text-xs">
                  <div className="flex items-center gap-2">
                    <div className="w-3 h-3 bg-blue-600 rounded"></div>
                    <span>Ships</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <div className="w-3 h-3 bg-green-500 rounded"></div>
                    <span>Systems</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <div className="w-3 h-3 bg-purple-500 rounded"></div>
                    <span>Equipment</span>
                  </div>
                </div>
              </div>
            </div>
          </Panel>
        </ReactFlow>
      </div>
    </div>
  );
};

export default FleetDiagramsApp;